.avatarWrapper {
    height: 350px;
    background: @primary-color;
    border-radius: 10px;
    position: relative;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.15);
}

.avatar {
    width: 80%;
    position: absolute;
    bottom: 0;
    border-radius: 0 0 10px 0;
    right: 0;
}

.avatarDesc {
    margin: 15px 10px 40px;
    .info {
        display: flex;
        justify-content: space-around;
    }
    .name {
        margin-top: 0 !important;
    }
}

.iconLink {
    display: inline-flex;
    transition: transform 0.2s @ease-base;
    border: none;

    &:hover {
        transform: scale(1.05);
    }
}

@primary-color: #407BFF;@link-color: @heading-color;@link-color-dark: fade(#fff, 100%);@link-hover-color: #407BFF;@font-size-base: 16px;@heading-color: #222;@text-color: #333;@ease-base: cubic-bezier(0,0,0,1);