.tag {
    border: solid 1px @primary-color;
    border-radius: 15px;
    font-size: 18px;
    padding: 3px 7px;
    color: @primary-color;
    margin: 3px;
    cursor: pointer;
    transition: all 0.5s @ease-base;
}

.tag:hover {
    color: white;
    background: @primary-color;
}

.tagList {
    display: flex;
    margin: 20px -5px;
    flex-wrap: wrap;
}

@primary-color: #407BFF;@link-color: @heading-color;@link-color-dark: fade(#fff, 100%);@link-hover-color: #407BFF;@font-size-base: 16px;@heading-color: #222;@text-color: #333;@ease-base: cubic-bezier(0,0,0,1);